.carousel {
  position: relative;
  height: 400px;
  overflow: hidden;

  &__gallery {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
  }

  &__item {
    flex: 0 0 100%;
    height: 100%;
  }

  &__image {
    height: 100%;
    width: 100%;
  }

  &__counters {
    position: absolute;
    bottom: 20px;
    left: 20px;
    display: flex;
  }
  
  &__counter {
    width: 18px;
    height: 18px;
    background: transparent;
    border: 2px solid $white;
    margin: 0 5px;
    cursor: pointer;
    transition: 0.5s;

    &--active {
      border: 2px solid $orange;
      background: $orange;
    }

    &:hover {
      border: 2px solid $orange;
    }
  }
}