.slider {
  width: 100%;
  margin-top: 15px;

  &__header {
    background: $white;
    width: 100%;
    display: flex;
  }

  &__title {
    margin-right: auto;
  }

  &__items {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    overflow-x: hidden;
  }

  &__item {
    flex: 0 0 33.33%;
    background: $white;
  }

  &__title {
    margin-left: 10px;
  }
}