.achivements {
  display: flex;
  height: 90px;
  background: $white;

  &__item {
    width: 100%;
    height: 80%;
    margin: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-right: 1px solid $gray;

    &:last-child {
      border-right: none;
    }
  }

  &__image {
    margin: 0 5px;
  }

  &__title {
    font-weight: 600;
  }
}