.sponsors {
  background: $white;
  width: 100%;

  &__wrapper {
    width: 100%;
    margin: 20px;
    overflow: hidden;
  }

  &__row {
    display: flex;

    &:first-child {
      margin-bottom: 20px;
    }
  }

  &__item {
    flex: 0 0 20%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__image {
    width: 131px;
    border: 1px solid $dark;
    padding: 3px;
  }
}